import { Link, NavLink, Switch, Route, useRouteMatch } from 'react-router-dom'

import ChatBar from '../chatbar/chatbar'

import './chat.scss'

import MessageBlock from '../messageBlock/messageBlock'

import Man from '../../assets/images/man.png'

const Chat = () => {

    const { path, url } = useRouteMatch()

    return(
        <div className="chat container">
            <div className="chat-left">
            <NavLink exact to={`${url}`} className='chat-left-chats' activeClassName="selected" >
                <ChatBar
                    img={Man}
                    name='Nematulloh Afri'
                    text='Продаётся Samsung 27" Full HD'
                    date='13 июнь, 10:33'
                />
            </NavLink>

            <NavLink to={`${url}/2`} className='chat-left-chats' activeClassName="selected" >
                <ChatBar
                    img={Man}
                    name='Nematulloh Afri'
                    text='Продаётся Samsung 27" Full HD'
                    date='13 июнь, 10:33'
                />
            </NavLink>

            <NavLink to={`${url}/3`} className='chat-left-chats' activeClassName="selected" >
                <ChatBar
                    img={Man}
                    name='Nematulloh Afri'
                    text='Продаётся Samsung 27" Full HD'
                    date='13 июнь, 10:33'
                />
            </NavLink>

            <NavLink to={`${url}/4`} className='chat-left-chats' activeClassName="selected" >
                <ChatBar
                    img={Man}
                    name='Nematulloh Afri'
                    text='Продаётся Samsung 27" Full HD'
                    date='13 июнь, 10:33'
                />
            </NavLink>

            <NavLink to={`${url}/5`} className='chat-left-chats' activeClassName="selected" >
                <ChatBar
                    img={Man}
                    name='Nematulloh Afri'
                    text='Продаётся Samsung 27" Full HD'
                    date='13 июнь, 10:33'
                />
            </NavLink>

            <NavLink to={`${url}/6`} className='chat-left-chats' activeClassName="selected" >
                <ChatBar
                    img={Man}
                    name='Nematulloh Afri'
                    text='Продаётся Samsung 27" Full HD'
                    date='13 июнь, 10:33'
                />
            </NavLink>

            <NavLink to={`${url}/7`} className='chat-left-chats' activeClassName="selected" >
                <ChatBar
                    img={Man}
                    name='Nematulloh Afri'
                    text='Продаётся Samsung 27" Full HD'
                    date='13 июнь, 10:33'
                />
            </NavLink>
                
            </div>

            <div className="chat-right">
                <Switch>
                    <Route exact path={`${path}`} component={MessageBlock} />
                </Switch>
            </div>
        </div>
    )
}

export default Chat;