import { Link } from 'react-router-dom'

import './header.scss'
import Proba from '../../components/proba/proba'
import Auth from '../../assets/images/auth.jpg'

import Logo from '../../assets/images/Logo.svg'

const Header = ({ deleteBlock, setDeleteBlock, noneBlock, token }) => {


    function ReloadOnce() {
        async function refreshPage() {
            window.location.reload();
        }

        const timer = setTimeout(() => {
            refreshPage();
        }, 10);
    }

    return (
        <div className="header">
            <div className="header-inner container">
                <div className="header-inner-top">
                    <div className="header-inner-top-left">
                        <Link
                            className='logo-wrapper'
                            to='/'
                            onClick={ReloadOnce}
                        >
                            <img src={Logo} alt="" className="logo-wrapper__img" />
                        </Link>
                    </div>

                    <div className="header-inner-top-center">
                        <Link className="header-inner-top-center__link">Объявления</Link>
                        <Link className="header-inner-top-center__link">Магазины</Link>
                        <Link className="header-inner-top-center__link">Для бизнеса</Link>
                        <Link className="header-inner-top-center__link">Помощь</Link>
                    </div>

                    <div className="header-inner-top-right">
                        <div className="header-inner-top-right-language">
                            <button className="header-inner-top-right-language__item">Рус</button>
                            <button className="header-inner-top-right-language__item">O’z</button>
                        </div>

                        {
                            token ? (
                                <Link to='/profile' className="auth">
                                    <h3 className="auth__name">Shon Parker</h3>
                                    <img src={Auth} alt="" className="auth__img"/>
                                </Link>
                            ) : (
                                <div className="header-inner-top-right-login">
                                    <Link to='/login' className="header-inner-top-right-login-sign">Вход</Link>
                                    <Link to='/login' className="header-inner-top-right-login-sign">Регистрация</Link>
                                </div>
                            )
                        }

                    </div>
                </div>

                <div className={`header-inner-bottom ${noneBlock ? 'delete' : ''}`}>
                    <form className="header-inner-bottom-left">
                        <select className="header-inner-bottom-left-select">
                            <option value="other" className="header-inner-bottom-left-select__item" selected disabled>
                                Любая категория
                            </option>
                            <option value="electronic" className="header-inner-bottom-left-select__item">
                                Электроника
                            </option>
                            <option value="work" className="header-inner-bottom-left-select__item">
                                Работа
                            </option>
                        </select>

                        <input type="search" className="header-inner-bottom-left__input" placeholder="Что будем искать ?" />

                        <select className="header-inner-bottom-left-select last">
                            <option value="" className="header-inner-bottom-left-select__item" selected disabled>По всей Узбекистану</option>
                            <option value="" className="header-inner-bottom-left-select__item">дфьвзцфьв</option>
                            <option value="" className="header-inner-bottom-left-select__item">лфцьв</option>
                        </select>

                        <Link
                            to='/choose'
                            className="header-inner-bottom-left__btn"
                            onClick={() => setDeleteBlock(!deleteBlock)}
                        >
                            Найти</Link>
                    </form>

                    <div className="header-inner-bottom-right">
                        <Link to='/additional' className="header-inner-bottom-right__btn">Добавить обьявления</Link>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Header;