import { Link } from 'react-router-dom'

import './wantedCard.scss'

const WantedCard = ({title, publish}) => {
    return(
        <a href='#' to='/sale' className='wanted-card'>
            <h3 className="wanted-card__title">
                {title}
            </h3>
            <h4 className="wanted-card__publish">
                {publish}
            </h4>
        </a>
    )
}

export default WantedCard