import { useState, lazy, Suspense, } from 'react'
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Redirect
} from 'react-router-dom'

import Header from './containers/header/header'
import SinglePage from './pages/singlepage/singlepage'
import Choose from './pages/choose/choose'
import Profile from './pages/profile/profile'
import Additional from './pages/additional/additional'

import './assets/styles/main.scss'
import Loginpage from './pages/loginPage/loginPage'

const LoginPage = lazy(() => import('./pages/loginPage/loginPage'))
const HomePage = lazy(() => import('./pages/homepage/homepage'))
// import HomePage from './pages/homepage/homepage'

function App() {

  const [deleteBlock, setDeleteBlock] = useState(false)

  const [token, setToken] = useState(window.localStorage.getItem('sessionToken'))

  return (

    <div className="App">
      <Router>
        <Suspense fallback={<h1>Loading...</h1>}>
          <Header
            token={token}
            noneBlock={deleteBlock}
            deleteBlock={deleteBlock}
            setDeleteBlock={setDeleteBlock}
          />

          {
            token ? (
              <Switch>
            <Redirect exact from='/' to='/home' />
            <Route path='/home' component={HomePage} />
            <Route path='/singlepage' component={SinglePage} />
            <Route path='/choose' component={Choose} />
            <Route path='/profile' component={Profile} />
            <Route path='/additional' component={Additional} />
            {/* <Route path='/login' component={LoginPage} /> */}
            <Route token={token} path='/login' component={HomePage} />
          </Switch>
            ) : (
              <Switch>
            <Redirect exact from='/' to='/home' />
            <Route path='/home' component={HomePage} />
            <Route path='/singlepage' component={SinglePage} />
            <Route path='/choose' component={Choose} />
            <Route path='/profile' component={Profile} />
            <Route path='/additional' component={Additional} />
            {/* <Route path='/login' component={LoginPage} /> */}
            <Route token={token} path='/login' render={() => <Loginpage setToken={setToken} /> } />
          </Switch>
            )
          }
          
        </Suspense>

      </Router>
    </div>
  );
}

export default App;
