import './loginPage.scss'

import { useRef,useState } from 'react'
import {Link} from 'react-router-dom'

import request from '../../services/http'

import Kivi from '../../assets/images/kivi.svg'

import NewFooter from '../../components/newFooter/newFooter'

const Loginpage = ({setToken}) => {

    console.log('eve.holt@reqres.in', 'cityslicka')

    const emailInputRef = useRef(null)
    const passwordInputRef = useRef(null)

    const [hasError, setHasError] = useState(false)

    const handleSubmit = (e) => {
        e.preventDefault();
        request.post('/login', {
            email: emailInputRef.current.value,
            password: passwordInputRef.current.value
        })
        .then((response) => {
            window.localStorage.setItem('sessionToken', response.data.token)
            setToken(response.data.token)
        })
        .catch((err) => setHasError(true))
    }
    
    
    return (
        <div className="login">
            <div className="login-inner container">
                <div className="login-inner-left">
                    <h3 className="login-inner-left__title">
                        Бесплатная регистрация
                </h3>

                    <form onSubmit={handleSubmit} className="login-inner-form">

                        <label htmlFor="input-email" className="login-inner-form__label">Введите email или номер телефона</label>
                        <input ref={emailInputRef} type="email" id='input-email' placeholder='99891 167 27 23' className="login-inner-form__input" />

                        <label htmlFor="input-name" className="login-inner-form__label">Пароль</label>
                        <input ref={passwordInputRef} type="password" id='input-name' placeholder='***********' className="login-inner-form__input" />

                        <button className="login-inner-form__btn">
                            Далее
                        </button>
                    </form>

                    <h3 className="or">
                        или
                </h3>

                    <a href="#" className="search-acc">
                        Войдите в систему, если у вас уже есть аккаунт kivi.uz
                </a>

                </div>

                <div className="login-inner-right">
                    <div className="open-place">
                        <img src={Kivi} className="open-place__img" />
                        <h5 className="open-place__text">
                            Откройте для себя
                            целую плошадку
                            для продаже
                    </h5>
                        <button className="open-place__btn">
                            Как это делать?
                    </button>
                    </div>
                </div>
            </div>
            <div className="fooooter">
                <NewFooter />
            </div>
        </div>
    )
}

export default Loginpage;