import './posterCard.scss'

import More from '../../assets/images/more.svg'

const PosterCard = ({img,title, price1,price2,data1,data2,data3,view,tel,saved}) => {
    return(
        <div className="poster-card">
            <div className="poster-card-top">
                <img src={img} alt="" className="poster-card-top__img"/>
                <h3 className="poster-card__title">
                    {title}
                </h3>

                <div className="poster-card-price">
                    <h3 className="poster-card-price__title">{price1}</h3>
                    <h3 className="poster-card-price__title">{price2}</h3>
                </div>

                <div className="poster-card-date">
                    <h4 className="poster-card__date">{data1}</h4>
                    <h4 className="poster-card__date">{data2}</h4>
                    <h4 className="poster-card__date">{data3}</h4>
                </div>

                <button className="poster-card-top__btn">Поднять</button>
                <button className="poster-card-top__btn btn--added">Рекламировать</button>

                <img src={More} alt="" className="poster-card-top__more"/>

            </div>

            <div className="poster-card-bottom">
                <h4 className="poster-card-bottom-statis">Статистика</h4>
                <h4 className="poster-card-date">Просмотры:{view}</h4>
                <h4 className="poster-card-date">Тел.:{tel}</h4>
                <h4 className="poster-card-date">В Избранном:{saved}</h4>

            </div>
        </div>
    )
}

export default PosterCard;