import './chatbar.scss'

import Script from '../../assets/images/script.svg'

const ChatBar = ({img,name,text,date}) => {
    return(
        <div className="chat-bar">
            <div className="chat-bar-left">
                <img src={img} alt="" className="chat-bar-left__img"/>
                <div className="chat-bar-left-inner">
                    <h3 className="chat-bar-left-inner__name">{name}</h3>
                    <h4 className="chat-bar-left-inner__text">{text}</h4>
                </div>
            </div>
            <div className="chat-bar-right">
                <img src={Script} alt="" className="chat-bar-right__img"/>
                <h4 className="chat-bar-right__date">{date}</h4>
            </div>
        </div>
    )
}

export default ChatBar