import Logo from '../../assets/images/footer-logo.svg'
import AppStore from '../../assets/images/app-store.svg';
import GPlay from '../../assets/images/g-plat.svg'

import './newFooter.scss'

const NewFooter = () => {
    return(
        <div className="footer">
            <div className="footer-top">
                <div className="footer-top-inner container">
                    <div className="footer-top-inner-left">
                        <a href="#">
                            <img src={Logo} alt="" className="footer-top-inner-left__img" />
                        </a>
                        <h3 className="footer-top-inner-left__text">
                            Продай, найди, купи все что ты пожелаешь
                        </h3>
                    </div>

                    <div className="footer-top-inner-center">
                        <h4 className="footer-top-inner-center__title">
                            Веб сайт бесплатных обьявлений | на базе имеется 234 944 999 шт обяления
                            </h4>
                    </div>

                    <div className="footer-top-inner-right">
                        <a
                            href="https://www.apple.com/app-store/"
                            target='_blank'
                            className="footer-top-inner-right-item"
                        >
                            <img src={AppStore} alt="" />
                        </a>
                        <a
                            href="https://play.google.com/store"
                            target='_blank'
                            className="footer-top-inner-right-item"
                        >
                            <img src={GPlay} alt="" />
                        </a>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default NewFooter;