import { Link } from 'react-router-dom'

import './saleCard.scss'

const SaleCard = ({ img, title, publish, price, top }) => {
    return (
        <Link to='/singlepage' className={`sale-card ${top ? 'top' : ''}`}>
            <img src={img} alt="" className="sale-card__img" />
            <div className="sale-card-bottom">
                <h3 className="sale-card__title">{title}</h3>
                <h5 className="sale-card__publish">{publish}</h5>
                <h4 className="sale-card__price">{price}</h4>
                <h3 className="sale-card-top">TOP</h3>
            </div>
        </Link>
    )
}

export default SaleCard;