import { Link, NavLink, Switch, Route, useRouteMatch } from 'react-router-dom'

import './profile.scss'

import PosterCard from '../../components/posterCard/posterCard'
import Poster from '../../assets/images/poster.png'
import Foooter from '../../components/newFooter/newFooter'
import Chat from '../../components/chat/chat'

const Profile = () => {

    const { path, url } = useRouteMatch()

    return (
        <>
            <div className="profile">
                <div className="profile-inner container">
                    <div className="profile-inner-header">
                        <div className="profile-inner-header-top">
                            <div className="profile-inner-header-top-left">
                                <h3 className="profile-inner-header-top-left__title">
                                    Здравствуйте Зухриддин Темиров
                        </h3>
                            </div>
                            <div className="profile-inner-header-top-right">
                                <h3 className="profile-inner-header-top-right__title">Ваш счет: 2 3000 сум</h3>
                                <button className="profile-inner-header-top-right__btn">Пополнить</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div className="profile-link container">
                <NavLink exact to={`${url}`} className='profile-link__link' activeClassName="selected" >Объявления</NavLink>
                <NavLink to={`${url}/chat`} className='profile-link__link' activeClassName="selected" >Сообщения</NavLink>
                <NavLink to={`${url}/account`} className='profile-link__link' activeClassName="selected" >Платежи и счёт</NavLink>
                <NavLink to={`${url}/settings`} className='profile-link__link' activeClassName="selected" >Настройки</NavLink>
                <NavLink to={`${url}/biznes`} className='profile-link__link' activeClassName="selected" >Мой бизнес</NavLink>
            </div>

            <div className="profile-main">
                <div className="profile-main-inner">

                    <Switch>
                        <Route exact path={`${path}`}>
                            <div className="menu-section container">
                                <h3 className="menu-section__title">Фото</h3>
                                <h3 className="menu-section__title">Название</h3>
                                <h3 className="menu-section__title">Цена</h3>
                                <h3 className="menu-section__title">Дата</h3>
                                <h3 className="menu-section__title">Действие</h3>
                            </div>
                            <div className="container">
                                <PosterCard
                                    img={Poster}
                                    title='Срочно нужен программист yii2'
                                    price1='2 000 000'
                                    price2='6 000 000 сум'
                                    data1='# 345 002'
                                    data2='С: 28 июнь'
                                    data3='По: 28 июль'
                                    view='195'
                                    tel='200'
                                    saved='0'
                                />

                                <PosterCard
                                    img={Poster}
                                    title='Срочно нужен программист yii2'
                                    price1='2 000 000'
                                    price2='6 000 000 сум'
                                    data1='# 345 002'
                                    data2='С: 28 июнь'
                                    data3='По: 28 июль'
                                    view='195'
                                    tel='200'
                                    saved='0'
                                />

                                <PosterCard
                                    img={Poster}
                                    title='Срочно нужен программист yii2'
                                    price1='2 000 000'
                                    price2='6 000 000 сум'
                                    data1='# 345 002'
                                    data2='С: 28 июнь'
                                    data3='По: 28 июль'
                                    view='195'
                                    tel='200'
                                    saved='0'
                                />

                                <PosterCard
                                    img={Poster}
                                    title='Срочно нужен программист yii2'
                                    price1='2 000 000'
                                    price2='6 000 000 сум'
                                    data1='# 345 002'
                                    data2='С: 28 июнь'
                                    data3='По: 28 июль'
                                    view='195'
                                    tel='200'
                                    saved='0'
                                />

                                <PosterCard
                                    img={Poster}
                                    title='Срочно нужен программист yii2'
                                    price1='2 000 000'
                                    price2='6 000 000 сум'
                                    data1='# 345 002'
                                    data2='С: 28 июнь'
                                    data3='По: 28 июль'
                                    view='195'
                                    tel='200'
                                    saved='0'
                                />
                            </div>

                            <div className="profile-footer">
                                <Foooter />
                            </div>

                        </Route>



                        <Route path={`${path}/chat`} component={Chat} />
                        <Route path={`${path}/account`} >Account</Route>
                        <Route path={`${path}/settings`} >Settings</Route>
                        <Route path={`${path}/biznes`} >Biznes</Route>
                    </Switch>

                </div>
            </div>

        </>
    )
}

export default Profile