import './messageBlock.scss'

import Man from '../../assets/images/man.png'
import Share from '../../assets/images/share.svg'
import Tv from '../../assets/images/tv.jpg'
import Script from '../../assets/images/script.svg'

const MessageBlock = () => {
    return(
        <div className="message-block">
            <div className="message-block-header">
                <div className="message-block-header-left">
                    <div className="message-block-header-left-top">
                        <img src={Man} alt="" className="message-block-header-left-top__img"/>
                        <h4 className="message-block-header-left-top__name">contact@jakubwp.com</h4>
                    </div>
                    <h3 className="last-seen">
                    Today, 6:02 AM
                    </h3>
                </div>
                <div className="message-block-header-right">
                    <a href="#">
                        <img src={Share} alt=""/>
                    </a>
                </div>
            </div>

            <div className="message-block-center">
                <img src={Tv} alt="" className="message-block-center__img"/>
                <img src={Tv} alt="" className="message-block-center__img"/>
                <img src={Tv} alt="" className="message-block-center__img"/>
            </div>

            <div className="owner-chat">
                <h3 className="owner-chat__name">contact@jakubwp.com</h3>
                <img src={Man} alt="" className="owner-chat__img"/>
            </div>

            <div className="message-block-footer">
                <textarea cols="30" rows="3" className="message-block-footer__text-holder"></textarea>
                <div className="send-block">
                    <div className="script-block">
                        <input type="file" className='send-file' id="send-file"/>
                        <label className='script-block__label' htmlFor="send-file">
                            <img src={Script} alt=""/>
                        </label>
                    </div>
                    <button className="send-block__btn">
                        Отправить
                    </button>
                </div>
            </div>
        </div>
    )
}

export default MessageBlock;