import './card.scss'

const Card = ({img,title,publish,price}) => {
    return(
        <a href='#' className="card">
            <img src={img} alt="" className="card__img"/>
            <h3 className="card__title">{title}</h3>
            <h5 className="card__publish">{publish}</h5>
            <h2 className="card__price">{price}</h2>
        </a>
    )
}

export default Card;